* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  text-decoration: none;
  list-style: none;
}

h1, h2, h3 {
  color: #004765;
}

nav {
  height: 80px;
  width: 100%;
  background: #ff9e16;
}

.enlace {
  padding: 20px 50px;
  position: absolute;
}

.logo {
  height: 40px;
}

nav ul {
  float: right;
  margin-right: 20px;
}

nav ul li {
  margin: 0 5px;
  line-height: 80px;
  display: inline-block;
}

nav ul li a {
  color: #004765;
  text-transform: uppercase;
  background-color: #fff;
  border-radius: 3px;
  padding: 7px 13px;
  font-size: 18px;
}

li a:hover {
  color: #fff;
  background-color: #004765;
  transition: all .5s;
}

li a:active {
  color: #fff;
  background-color: #004765;
}

.checkbtn {
  color: #fff;
  float: right;
  cursor: pointer;
  margin-right: 40px;
  font-size: 30px;
  line-height: 80px;
  display: none;
}

#check {
  display: none;
}

#map {
  height: 80vh;
  width: 80%;
}

.body {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.index-flex1 {
  flex-direction: row;
  display: flex;
}

p {
  width: 80%;
  line-height: 25px;
}

.google-btn {
  color: #fff;
  background-color: #004765;
  border-radius: 8px;
  padding: 15px 25px;
  text-decoration: none;
}

.google-btn:hover {
  background-color: #00364d;
}

#google-maps {
  color: #fff;
  cursor: pointer;
  background-color: #004765;
  border-radius: 8px;
  padding: 15px 25px;
  text-decoration: none;
}

.yellow-bg {
  color: #004765;
  background-color: #ff9e16;
  justify-content: center;
  align-items: center;
  margin: 10px;
  padding: 10px;
}

h1 {
  padding: 30px 10px;
  font-size: 50px;
}

h2 {
  padding: 10px;
  font-size: 30px;
}

#google-maps, .google-btn {
  margin: 5px 15px;
}

footer {
  width: 100%;
  text-align: left;
  color: #fff;
  background: #000;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  padding: 20px 20px 40px;
}

.presentacion .body img {
  margin: 15px 0;
}

.enlaces h3, .detalle h3, li {
  margin: 20px;
}

@media (max-width: 952px) {
  .enlace {
    padding-left: 20px;
  }

  nav ul li a {
    font-size: 16px;
  }
}

@media (max-width: 858px) {
  .checkbtn {
    display: block;
  }

  nav ul {
    width: 100%;
    height: 100vh;
    text-align: center;
    background: #ff9e16;
    transition: all .5s;
    position: fixed;
    top: 80px;
    left: -100%;
  }

  nav ul li {
    margin: 50px 0;
    line-height: 30px;
    display: block;
  }

  nav ul li a {
    font-size: 20px;
  }

  li a:hover, li a.active {
    color: #fff;
    background: #004765;
  }

  #check:checked ~ ul {
    left: 0;
  }

  #map {
    height: 80vh;
    width: 100%;
  }

  #google-maps, .google-btn {
    margin: 5px 15px;
    display: block;
  }
}

